.profile-dropdown {
  display: block;
}

.profile-dropdown-items {
  width: 170px;
  i {
    vertical-align: middle;
    margin-right: 5px;
  }
}

.nav-user {
  padding: 0 12px !important;
  img {
    height: 32px;
    width: 32px;
  }
}
